import React, { useState, useEffect } from "react";
import { AppBar, Toolbar, IconButton, Box, InputBase } from "@mui/material";
import { Link } from "react-router-dom";
import HomeIcon from "@mui/icons-material/Home";
import SearchIcon from "@mui/icons-material/Search";
import CloseIcon from "@mui/icons-material/Close";
import NavBarButton from "components/ui/buttons/NavBarButton";

const menuItems = [
  { label: "About", link: "/about" },
  { label: "Projects", link: "/projects" },
  { label: "Resume", link: "/resume" },
  { label: "Blogs", link: "/blogs" },
];

const NavBar = React.memo(function NavBar() {
  const [searchQuery, setSearchQuery] = useState("");
  const [isScrolled, setIsScrolled] = useState(false);
  const [showSearchBar, setShowSearchBar] = useState(false);
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    const checkIsMobile = () => {
      const userAgent = navigator.userAgent || navigator.vendor || window.opera;
      const isMobileDevice = /Mobi|Android|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(
        userAgent
      );
      setIsMobile(isMobileDevice);
    };

    checkIsMobile();
  }, []);

  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 50);
    };

    window.addEventListener("scroll", handleScroll);

    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  const handleSearch = () => {
    if (searchQuery.trim() !== "") {
      console.log("Searching for:", searchQuery);
      setSearchQuery(""); 
      setShowSearchBar(false); 
    }
  };

  const toggleSearchBar = () => {
    setShowSearchBar((prev) => !prev);
  };

  const renderSearchBar = (isMobileView) => (
    <InputBase
      aria-label="search"
      value={searchQuery}
      onChange={(e) => setSearchQuery(e.target.value)}
      onKeyDown={(e) => e.key === "Enter" && handleSearch()}
      sx={{
        backgroundColor: "white",
        padding: "5px 10px",
        borderRadius: "20px",
        width: isMobileView ? "100%" : "200px",
      }}
    />
  );

  const renderSearchSection = () => {
    if (isMobile) {
      return (
        <>
          <IconButton
            color="inherit"
            aria-label={showSearchBar ? "Close search" : "Open search"}
            onClick={toggleSearchBar}
          >
            {showSearchBar ? <CloseIcon /> : <SearchIcon />}
          </IconButton>
          {showSearchBar && (
            <Box
              sx={{
                position: "absolute",
                top: "70px",
                left: "50%",
                transform: "translateX(-50%)",
                backgroundColor: "white",
                padding: "5px 10px",
                borderRadius: "20px",
                boxShadow: "0px 4px 10px rgba(0, 0, 0, 0.2)",
                width: "90%",
              }}
            >
              {renderSearchBar(true)}
            </Box>
          )}
        </>
      );
    }
    return (
      <Box sx={{ display: "flex", alignItems: "center", gap: "10px" }}>
        {renderSearchBar(false)}
        <IconButton
          color="inherit"
          onClick={handleSearch}
          sx={{ marginLeft: "5px" }}
        >
          <SearchIcon />
        </IconButton>
      </Box>
    );
  };

  return (
    <AppBar
      position="fixed"
      sx={{
        backgroundColor: isScrolled ? "rgba(51, 51, 51, 0.8)" : "#333",
        transition: "background-color 0.3s ease",
      }}
    >
      <Toolbar sx={{ display: "flex", justifyContent: "space-between" }}>
        <Box sx={{ display: "flex", alignItems: "center" }}>
          <IconButton component={Link} to="/" color="inherit" aria-label="home">
            <HomeIcon sx={{ fontSize: "2rem", color: "white" }} />
          </IconButton>
        </Box>

        <Box
          sx={{
            display: "flex",
            alignItems: "center",
            flexGrow: 1,
            justifyContent: "flex-end",
            gap: "15px",
          }}
        >
          {!isMobile &&
            menuItems.map((item, index) => (
              <NavBarButton
                key={index}
                component={Link}
                to={item.link}
                color="inherit"
                aria-label={`Navigate to ${item.label}`}
              >
                {item.label}
              </NavBarButton>
            ))}

          {renderSearchSection()}
        </Box>
      </Toolbar>
    </AppBar>
  );
});

export default NavBar;