import React from 'react';
import { Box, List, Stack, useMediaQuery, useTheme } from '@mui/material';
import SidebarButton from 'components/ui/buttons/SidebarButton';

const PostSidebar = ({ content }) => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));

  if (isMobile) {
    return null;
  }

  const styles = {
    container: {
      position: 'fixed',
      top: '50%',
      right: '30px',
      transform: 'translateY(-50%)',
      zIndex: theme.zIndex.drawer + 1,
      width: '150px',
      padding: '20px',
      borderRadius: '8px',
    },
    title: {
      textAlign: 'left',
      fontWeight: 'bold',
      fontSize: '18px',
      marginBottom: '10px',
    },
  };

  if (!content || content.length === 0) {
    return (
      <Stack sx={styles.container}>
        <Box sx={{ textAlign: 'center', fontWeight: 'bold', fontSize: '18px' }}>No contents available</Box>
      </Stack>
    );
  }

  return (
    <Stack sx={styles.container}>
      <Box sx={styles.title}>Contents</Box>
      <List>
        {content.map((section, sectionIndex) =>
          section.sidebarTitles.map(({ title, id }, titleIndex) => (
            <SidebarButton
              key={`${sectionIndex}-${titleIndex}`}
              title={title}
              id={id}
            />
          ))
        )}
      </List>
    </Stack>
  );
};

export default PostSidebar;
