import React from 'react';
import { Card, CardMedia, CardContent, Typography, Box, Button } from '@mui/material';
import { styled } from '@mui/system';
import { useNavigate } from 'react-router-dom';
import TagButton from 'components/ui/buttons/TagButton';

const StyledCard = styled(Card)(({ theme }) => ({
  width: '100%',
  maxWidth: '600px',
  margin: '0 auto',
  position: 'relative',
  backgroundColor: 'white',
  boxShadow: 'none',
  padding: '16px',
  [theme.breakpoints.down('md')]: {
    maxWidth: '500px',
    padding: '12px',
  },
  [theme.breakpoints.down('sm')]: {
    maxWidth: '100%',
    padding: '8px',
  },
}));

const StyledMedia = styled(CardMedia)(({ theme }) => ({
  height: 400,
  cursor: 'pointer',
  [theme.breakpoints.down('md')]: {
    height: 300,
  },
  [theme.breakpoints.down('sm')]: {
    height: 200,
  },
}));

const StyledTitle = styled(Typography)(({ theme }) => ({
  cursor: 'pointer',
  color: theme.palette.primary.main,
  fontSize: '1.5rem',
  [theme.breakpoints.down('md')]: {
    fontSize: '1.25rem',
  },
  [theme.breakpoints.down('sm')]: {
    fontSize: '1rem',
  },
}));

const StyledDescription = styled(Typography)(({ theme }) => ({
  fontSize: '1rem',
  [theme.breakpoints.down('md')]: {
    fontSize: '0.875rem',
  },
  [theme.breakpoints.down('sm')]: {
    fontSize: '0.75rem',
  },
}));

const TagsContainer = styled(Box)(({ theme }) => ({
  display: 'flex',
  flexWrap: 'wrap',
  gap: '8px',
  position: 'absolute',
  bottom: 16,
  right: 16,
  [theme.breakpoints.down('sm')]: {
    flexWrap: 'nowrap',
    gap: '4px',
  },
}));

const BlogCard = ({ post }) => {
  const navigate = useNavigate();

  const handleNavigate = (path) => {
    navigate(path);
  };

  const renderTags = () => {
    if (!post.tags || post.tags.length === 0) {
      return <Typography variant="body2" color="textSecondary">No tag</Typography>;
    }

    const isMobile = window.innerWidth <= 600;

    if (isMobile) {
      return (
        <>
          <TagButton tag={post.tags[0]} onClick={() => handleNavigate(`/tags/${post.tags[0]}`)} />
          {post.tags.length > 1 && (
            <Button
              variant="text"
              size="small"
              sx={{ color: 'gray' }}
              onClick={() => handleNavigate(`/tags`)}
            >
              +{post.tags.length - 1} more
            </Button>
          )}
        </>
      );
    }

    return post.tags.map((tag, index) => (
      <TagButton key={index} tag={tag} onClick={() => handleNavigate(`/tags/${tag}`)} />
    ));
  };

  return (
    <StyledCard>
      <StyledMedia
        component="img"
        image={post.imageUrl}
        alt={`Image for ${post.title}`}
        onClick={() => handleNavigate(`/blog/${post.id}`)}
      />
      <CardContent>
        <StyledTitle
          variant="h5"
          onClick={() => handleNavigate(`/blog/${post.id}`)}
          aria-label={`Navigate to blog post titled ${post.title}`}
        >
          {post.title}
        </StyledTitle>
        <StyledDescription variant="body2">
          {post.description}
        </StyledDescription>
      </CardContent>

      <TagsContainer>{renderTags()}</TagsContainer>
    </StyledCard>
  );
};

export default BlogCard;
