import * as React from 'react';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import Divider from '@mui/material/Divider';
import IconButton from '@mui/material/IconButton';
import Link from '@mui/material/Link';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
import GitHubIcon from '@mui/icons-material/GitHub';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import X from '@mui/icons-material/X';
import { createTheme, ThemeProvider } from '@mui/material/styles';

const theme = createTheme({
  components: {
    MuiIconButton: {
      styleOverrides: {
        root: {
          '&:hover': {
            transform: 'scale(1.2)',
            transition: 'transform 0.3s ease-in-out',
          },
        },
      },
    },
  },
});

function SocialLink({ href, label, icon }) {
  return (
    <IconButton 
      color="inherit" 
      size="small" 
      href={href} 
      target="_blank" 
      rel="noopener noreferrer"   
      aria-label={`Visit ${label} profile`}
    >
      {icon}
    </IconButton>
  );
}

function Copyright() {
  return (
    <Typography 
      component="p" 
      variant="body2" 
      sx={{ color: 'text.secondary', mt: 1, textAlign: 'center' }}
      aria-hidden="true" 
    >
      {'Copyright © '}
      {/* 이후 실제 주소로 변경 */}
      <Link color="text.secondary" href="https://abc/" aria-label="Go to Chrono Pilferer's Website">
        Chrono Pilferer
      </Link>
      &nbsp;
      {new Date().getFullYear()}
    </Typography>
  );
}

// 이후 실제 주소로 변경
export default function Footer() {
  const socialLinks = [
    { href: 'https://github.com/your-profile', label: 'GitHub', icon: <GitHubIcon /> },
    { href: 'https://x.com/your-profile', label: 'X', icon: <X /> },
    { href: 'https://linkedin.com/in/your-profile', label: 'LinkedIn', icon: <LinkedInIcon /> },
  ];

  return (
    <ThemeProvider theme={theme}>
      <footer>
        <Divider />
        <Container>
          <Box 
            sx={{ 
              display: 'flex', 
              justifyContent: 'center', 
              alignItems: 'center', 
              pt: { xs: 4, sm: 8 }, 
              width: '100%' 
            }}
          >
            <Stack direction="row" spacing={2}>
              {socialLinks.map((link) => (
                <SocialLink key={link.label} {...link} />
              ))}
            </Stack>
          </Box>

          <Box sx={{ mt: { xs: 4, sm: 8 } }}>
            <Copyright />
          </Box>
        </Container>
      </footer>
    </ThemeProvider>
  );
}