import React, { useCallback } from 'react';
import { Box, Typography } from '@mui/material';
import PostSidebar from 'components/post/PostSidebar';

// 제목 이미지 컴포넌트
const PostTitleImage = ({ title, titleImage }) => (
  <Box sx={{ textAlign: 'center', marginBottom: '20px' }}>
    <img
      src={titleImage}
      alt={`post image: ${title}`}
      style={{ maxWidth: '100%', height: 'auto', borderRadius: '8px' }}
    />
  </Box>
);

// 본문 내용 컴포넌트
const PostContent = ({ content }) => (
  <Box sx={{ flex: 1, maxWidth: '800px', textAlign: 'left' }}>
    {content.length > 0 ? (
      content.map((section, index) => (
        <Box key={index} id={`section-${index}`} sx={{ marginBottom: '20px' }}>
          {section.renderedContent}
        </Box>
      ))
    ) : (
      <Typography sx={{ textAlign: 'center', color: 'gray' }}>
        No content available. Check back later!
      </Typography>
    )}
  </Box>
);

const PostTemplates = ({ title, titleImage, content = [] }) => {

    const handleScrollToSection = useCallback((index) => {
    const sectionElement = document.getElementById(`section-${index}`);
    if (sectionElement) {
      sectionElement.scrollIntoView({ behavior: 'smooth', block: 'center' });
    }
  }, []);

  return (
    <Box sx={{ padding: '20px' }}>
      {/* 제목 이미지 */}
      <PostTitleImage title={title} titleImage={titleImage} />

      {/* 블로그 제목 */}
      <Box display="flex" justifyContent="center" alignItems="center" height="10vh">
        <Typography
          variant="h4"
          component="h1"
          gutterBottom
          aria-label={`title: ${title}`}
        >
          {title}
        </Typography>
      </Box>

      {/* 본문과 사이드바 컨테이너 */}
      <Box
        sx={{
          display: 'flex',
          flexDirection: { xs: 'column', md: 'row' },
          justifyContent: 'center',
          alignItems: 'flex-start',
        }}
      >
        {/* 본문 내용 */}
        <PostContent content={content} />

        {/* 사이드바 */}
        <Box>
          <PostSidebar
            content={content.map((section) => ({ sidebarTitles: section.sidebarTitles }))}
            onSectionClick={handleScrollToSection}
          />
        </Box>
      </Box>
    </Box>
  );
};

export default PostTemplates;

