import React from 'react';
import { ThemeProvider } from '@mui/material/styles';
import { Routes, Route } from 'react-router-dom';
import theme from 'styles/theme';
import Layout from 'components/layout/Layout';
import HomePage from 'pages/HomePage';
import SampleBlogPostPage from 'pages/SampleBlogPostPage';
import SampleBlogPostPage2 from 'pages/SampleBlogPostPage2';

function App() {
  return (
    <ThemeProvider theme={theme}>
        <Layout>
          <Routes>
            <Route path="/" element={<HomePage />} />
            <Route path="/blog/1" element={<SampleBlogPostPage />} />
            <Route path="/blog/2" element={<SampleBlogPostPage2 />} />
          </Routes>
        </Layout>
    </ThemeProvider>
  );
}

export default App;
