import React from 'react';
import { Box } from '@mui/material';
import BlogCard from 'components/ui/cards/BlogCard';
import sampleImage1 from 'assets/images/sample_image_1.png';
import sampleImage2 from 'assets/images/sample_image_2.png';

const BlogCardContainer = () => {
  const blogPosts = [
    { id: 1, title: '첫 번째 블로그 글', description: '이것은 첫 번째 블로그 글입니다.', imageUrl: sampleImage1, tags: ['React', 'Material-UI', 'JavaScript'] },
    { id: 2, title: '두 번째 블로그 글', description: '이것은 두 번째 블로그 글입니다.', imageUrl: sampleImage1, tags: ['React', 'Material-UI'] },
    { id: 3, title: '세 번째 블로그 글', description: '이것은 세 번째 블로그 글입니다.', imageUrl: sampleImage1, tags: ['jpa'] },
    { id: 4, title: '네 번째 블로그 글', description: '이것은 네 번째 블로그 글입니다.', imageUrl: sampleImage2, tags: ['python'] },
    { id: 5, title: '다섯 번째 블로그 글', description: '이것은 다섯 번째 블로그 글입니다.', imageUrl: sampleImage2, tags: ['ML'] },
  ];

  return (
    <Box
      sx={{
        display: 'flex',
        justifyContent: 'center', 
        alignItems: 'center', 
        flexDirection: 'column', 
        width: '100%',
      }}
    >
      {blogPosts.map((post) => (
        <BlogCard key={post.id} post={post} />
      ))}
    </Box>
  );
};

export default BlogCardContainer;
